<template>
  <div >
    <h1> 源龙后台管理 </h1>
	<el-row :gutter="20">
	  <el-col :span="12" :offset="6">
		<div >
			<el-form ref="form" :model="form" label-width="80px">
			  <el-form-item label="用户名">
			    <el-input v-model="form.name"></el-input>
			  </el-form-item>
			  <el-form-item label="密码">
			    <el-input v-model="form.password"></el-input>
			  </el-form-item>
			  <el-form-item>
			    <el-button type="primary" @click="login">登录</el-button>
			    <el-button>取消</el-button>
			  </el-form-item>
			</el-form>
		</div>
	  </el-col>
	</el-row>
    </div>
</template>

<script>
	import {login} from '../api/index.js'
export default {
  name: 'HelloWorld',
  data(){
	  return{
		  form:{
			name:"",
			password:"",
		  }
	  }
  },
  methods:{
	  login() {
			login({name:this.form.name,password:this.form.password}).then(r => {
			  // console.log("返回："+JSON.stringify(r))
			  if(r.status==200 && r.data!=''){
				  console.log("token:"+r.data);
				  this.$store.commit('set_token',r.date)
				  sessionStorage.token = r.data
				  this.$router.push({path:"/order"})	
				  console.log("store_token:"+this.$store.state.token)
			  }
			})
	  }
  },
  props: {
    msg: String
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scss scoped>
h1{
	background: #42B983;
}

</style>
